export interface ConnectivityPlan {
  grantedConnections: string;
  id: string;
  name: string;
  ref: string;
  usedConnections: string;
  message?: string;
}

export interface DeviceType {
  name: string;
  id: string;
}

export interface DeviceProfile {
  id: string;
  name: string;
  typeMAC: string;
  message?: string;
}

export interface ActilityDeviceRegistration extends Connection {
  applicationEUI: string;
  applicationKey: string;
  devEUI: string;
  deviceProfile: DeviceProfile;
  connectivityPlan: ConnectivityPlan;
  deviceType: DeviceType;
  lnsConnectionName: string;
}

export interface Connection {
  connection: {
    name: string;
    description: string;
    baseUrl: string;
    profileId: string;
    username: string;
    password: string;
  };
}

export const PRODUCT_EXPERIENCE = {
  EVENT: 'deviceRegistration',
  COMPONENT: 'actility-registration',
  RESULT: { SUCCESS: 'registrationSuccess', FAILURE: 'registrationFailure' }
};
