import { Injectable } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import {
  FetchClient,
  IFetchOptions,
  ApplicationService,
  IManagedObject,
  InventoryService,
  IResultList
} from '@c8y/client';
import { AppStateService, gettext, OptionsService } from '@c8y/ngx-components';
import {
  ActilityDeviceRegistration,
  ConnectivityPlan,
  DeviceProfile
} from './actility-device-registration.model';
import { some } from 'lodash-es';

export enum ErrorName {
  NoConnectivityPlansError = 'NoConnectivityPlansError',
  NoFreeSlotsInConnectivityPlansError = 'NoFreeSlotsInConnectivityPlansError',
  NoConnectivitySettingsError = 'NoConnectivitySettingsError',
  ConnectivitySettingsError = 'ConnectivitySettingsError',
  NoDeviceProfilesError = 'NoDeviceProfilesError',
  DeviceProfilesFetchError = 'DeviceProfilesFetchError',
  NoDeviceProtocolsError = 'NoDeviceProtocolsError',
  DeviceProtocolsFetchError = 'DeviceProtocolsFetchError',
  RegistrationError = 'RegistrationError'
}

@Injectable({ providedIn: 'root' })
export class ActilityDeviceRegistrationService {
  private baseUrl = '/service/actility';
  private registrationUrl = `${this.baseUrl}/newDeviceRequest`;
  private connectivityPlansUrl = `${this.baseUrl}/connectivityPlans`;
  private deviceProfilesUrl = `${this.baseUrl}/deviceProfiles`;
  private headers: object = {
    'Content-Type': 'application/json'
  };

  constructor(
    private inventoryService: InventoryService,
    private client: FetchClient,
    private translateService: TranslateService,
    private applicationService: ApplicationService,
    private optionsService: OptionsService,
    private appState: AppStateService
  ) {}

  async getConnections() {
    const options: IFetchOptions = {
      method: 'GET',
      headers: this.headers
    };
    const res = await this.client.fetch(`${this.baseUrl}/lns-connection`, options);
    const data = await res.json();

    if (res.status === 200) {
      if (data.length === 0) {
        await this.throwNoConnectivitySettingsError();
      }
    } else {
      await this.throwConnectivitySettingsError(data);
    }
    return { res, data };
  }
  /**
   * Gets connectivity plans from LoRa platform.
   * @param connectionName The name of connection for which connectivity plans will be retrieved
   * @returns The result list with connectivity plans, or throws an error with exception.
   */
  async getConnectivityPlans(connectionName: string): Promise<IResultList<ConnectivityPlan>> {
    const options: IFetchOptions = {
      method: 'GET',
      headers: this.headers,
      params: {
        actilityConnectionName: connectionName
      }
    };

    const res = await this.client.fetch(this.connectivityPlansUrl, options);
    const data = await res.json();

    if (res.status === 200) {
      if (data.length === 0) {
        this.throwNoConnectivityPlansError();
      } else {
        if (!this.hasAvailableConnections(data)) {
          this.throwNoFreeSlotsInConnectivityPlansError();
        }
      }
    } else {
      await this.throwConnectivitySettingsError(data);
    }

    return { res, data };
  }

  /**
   * Gets the device profiles from LoRa platform.
   * @param connectionName The name of connection for which device profiles will be retrieved
   * @returns The result list with device profiles, or throws an error with exception.
   */
  async getDeviceProfiles(connectionName: string): Promise<IResultList<DeviceProfile>> {
    const options: IFetchOptions = {
      method: 'GET',
      headers: this.headers,
      params: {
        actilityConnectionName: connectionName
      }
    };

    const res = await this.client.fetch(this.deviceProfilesUrl, options);
    const data = await res.json();

    if (res.status === 200) {
      if (data.length === 0) {
        this.throwNoDeviceProfilesError();
      }
    } else {
      this.throwDeviceProfilesFetchError();
    }

    return { res, data };
  }

  /**
   * Gets the device protocols
   */
  async getDeviceProtocols(
    filter: object = { withTotalPages: true }
  ): Promise<IResultList<IManagedObject>> {
    const query = {
      __filter: {
        __and: [
          { __has: 'c8y_IsDeviceType' },
          {
            type: { __in: ['c8y_ActilityDeviceType', 'c8y_LoraDeviceType', 'c8y_LpwanDeviceType'] }
          }
        ]
      },
      __orderby: [{ name: 1 }]
    };
    const deviceProtocolsList = await this.inventoryService.listQuery(query, filter);
    const { res, data } = deviceProtocolsList;

    if (res.status === 200) {
      if (data.length === 0) {
        this.throwNoDeviceProtocolsError();
      }
    } else {
      this.throwDeviceProtocolsFetchError();
    }

    return deviceProtocolsList;
  }

  /**
   * Creates device registration
   */
  async register(registration: ActilityDeviceRegistration) {
    const options: IFetchOptions = {
      method: 'POST',
      headers: this.headers,
      body: JSON.stringify(registration)
    };

    const res = await this.client.fetch(this.registrationUrl, options);
    const data = await res.json();

    if (res.status !== 201) {
      this.throwRegistrationError(data);
    }

    return { res, data };
  }

  /**
   * checks if used connections is less then granted connections
   */
  private hasAvailableConnections(connectivityPlans) {
    return some(
      connectivityPlans,
      plan => parseInt(plan.grantedConnections, 10) > parseInt(plan.usedConnections, 10)
    );
  }

  private async throwNoConnectivitySettingsError() {
    const error = new Error();
    error.name = ErrorName.NoConnectivitySettingsError;

    if (await this.appState.isApplicationAvailable('administration')) {
      error.message = this.translateService.instant(
        gettext(
          `Could not get connectivity plans from the LoRa platform. Verify the ThingPark credentials in the Administration application under <a href="{{ link }}">Settings</a>.`
        ),
        {
          link: '/apps/administration/index.html#/connectivitySettings/actility_lora_provider_settings'
        }
      );
    } else {
      error.message = gettext(
        'Could not get connectivity plans from the LoRa platform. Please contact the administrator.'
      );
    }

    throw error;
  }

  private throwConnectivitySettingsError(data: { message: string }) {
    const error = new Error();
    error.name = ErrorName.ConnectivitySettingsError;
    error.message = data.message;
    throw error;
  }

  private throwNoConnectivityPlansError() {
    const error = new Error();
    error.name = ErrorName.NoConnectivityPlansError;
    error.message = gettext(
      'No connectivity plans found. New connectivity plans must be created via the LoRa platform.'
    );
    throw error;
  }

  private throwNoFreeSlotsInConnectivityPlansError() {
    const companyName = this.optionsService.get('companyName', 'Cumulocity IoT');
    const error = new Error();
    error.name = ErrorName.NoFreeSlotsInConnectivityPlansError;
    error.message = this.translateService.instant(
      gettext(
        `No connectivity plans with free slots available. Please contact ThingPark on the device quota limits for your connectivity plans or remove unused devices from ThingPark and retry registering the device in the {{companyName}} platform.`
      ),
      {
        companyName
      }
    );
    throw error;
  }

  private throwDeviceProfilesFetchError() {
    const error = new Error();
    error.name = ErrorName.DeviceProfilesFetchError;
    error.message = gettext('Could not load device profiles from the LoRa platform.');
    throw error;
  }

  private throwNoDeviceProfilesError() {
    const error = new Error();
    error.name = ErrorName.NoDeviceProfilesError;
    error.message = gettext(
      'No device profiles found. Create a new device profile via the LoRa platform.'
    );
    throw error;
  }

  private throwDeviceProtocolsFetchError() {
    const error = new Error();
    error.name = ErrorName.DeviceProtocolsFetchError;
    error.message = gettext('Could not load device protocols.');
    throw error;
  }

  private throwNoDeviceProtocolsError() {
    const error = new Error();
    error.name = ErrorName.NoDeviceProtocolsError;
    error.message = this.translateService.instant(
      gettext(
        `No device protocols configured. Create a LoRa device protocol in <a href="{{ link }}">Device protocols</a>.`
      ),
      {
        link: '/apps/devicemanagement/#/deviceprotocols'
      }
    );
    throw error;
  }

  private throwRegistrationError(data: { message: string }) {
    const error = new Error();
    error.name = ErrorName.RegistrationError;
    error.message = data.message;
    throw error;
  }
}
