import { Injectable } from '@angular/core';
import { TenantUiService } from '@c8y/ngx-components';
import { ActilityDeviceRegistrationButtonComponent } from './actility-device-registration-button.component';
import { RegisterDeviceFactory, RegisterDeviceItem } from '@c8y/ngx-components/register-device';
@Injectable({
  providedIn: 'root'
})
export class ActilityDeviceRegistrationFactory implements RegisterDeviceFactory {
  constructor(private tenantService: TenantUiService) {}

  get() {
    const actions: RegisterDeviceItem[] = [];
    if (this.tenantService.isMicroserviceSubscribedInCurrentTenant('actility')) {
      actions.push({
        template: ActilityDeviceRegistrationButtonComponent,
        priority: 99,
        category: 'single'
      } as RegisterDeviceItem);
    }
    return actions;
  }
}
